body{
    background-color: white;
    color: black;
    list-style: none;
    padding: 0;
}

.navigation{
    background: whitesmoke;
    padding: 2% 0% 2% 0%;
}

ul {
    display: flex;
    list-style: none;
    text-decoration: none;
    padding: 0%;
    font-size: 100%;
}

ul li{
    float: right;
    font-size: 100%;
    padding:0% 3% 0% 3%;
    text-decoration: none;
    color: black;
}

ul li a{
    font-size: 21px;
    text-decoration: none;
    color: black;
    font-weight: 500;
    font-weight: 700;
    font-size: 20px;
}
