body{
    background: transparent;
    list-style: none;
    padding: 0;
    justify-content: center;
    align-items: center;
    background-image: url('../Images/new_background.jpg');
    background-color: rgba(255, 255, 255, 0.2);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.prof-container{
    justify-content: center;
    align-items: center;
    text-align: center;
    display: flex;
    float: left;
    position: relative;
    width: 80%;
    color: white;
    margin-top: 5%;
    margin-bottom: 5%;
    margin-left: 10%;
    margin-right: 10%;
    padding: 10px;
}

.prof-container .prof-right{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    border-radius: 20px;
    background: rgba(255,69,0,0.8);
    width: 80%;
    padding: 5%;
}

.prof-container .prof-right h5{
    font-style: normal;
    font-size: 18px;
    font-weight: 700;
    padding-left: 4%;
    padding-right: 4%;
    padding-top: 4%;
    padding-bottom: 1%;
    justify-content: center;
    text-align: center;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    color: white;
}

.prof-container .prof-right .contact-details{
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 2%;
    background: rgb(249,241,240);
    border-radius: 15px;
}

.prof-container .prof-right .contact-details p{
    color: black;
    padding-top: 5%;
    margin-top: 2%;
}

.prof-container .prof-right .contact-details h3{
    padding-top: 5%;
    margin-top: 2%;
    font-size: 15px;
    text-align: center;
    color: black;
}

.prof-container .prof-right .form-wrapper{
    width: 60%;
    padding: 1%;
    box-sizing: border-box;
    margin: auto;
}

.prof-container .prof-right .form-wrapper .input-field{
    padding: 3%;
    margin: 1%;
    width: 100%;
    border-radius: 10px;
    border: none;
}

.prof-container .prof-right .submit button{
    border: none;
    width: 50%;
    margin-top: 10%;
    padding-left: 5%;
    padding-right: 5%;
    height: 40px;
    background: white;
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    color: black;
    font-size: 18px;
    font-weight: 500;
}


.prof-container .prof-right .submit button:hover{
    background: rgb(63, 63, 63);
    border-radius: 15px;
    justify-content: center;
    align-items: center;
    text-align: center;
    justify-items: center;
    margin-left: auto;
    margin-right: auto;
    margin-top: auto;
    margin-bottom: auto;
    color: white;
    font-size: 18px;
    font-weight: 500;
}


ul li{
    padding: 4%;
    color: black;
    list-style: none;
    list-style-type:none;
    justify-content: center;
}

.fab{
    padding: 4%;
    color: black;
    list-style: none;
    list-style-type:none;
    justify-content: center;
    align-items: center;
    justify-items: center;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

/* card */

.card {
    margin-top: 5%;
    width: 90%;
    box-shadow: 0 4px 8px 0 rgba(255, 255, 255, 0.7);
    /* max-width: 300px; */
    margin: auto;
    text-align: center;
    font-family: arial;
    background: whitesmoke;
  }
  
  .card h1{

      margin: 2% auto;
      color: black;
      padding: 2%;
      font-weight: 700;
      width: 80%;
      font-size: 20px;
      justify-content: center;
      align-items: center;
      text-align: center;
      background: rgba(255, 255, 255, 0.2);
  }

  .card p{
      color: black;
  }

  .price a{
    color: black;
    font-size: 22px;
    text-decoration: none;
  }
  
  .card button {
    border: none;
    outline: 0;
    padding: 12px;
    color: white;
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
    border-radius: 5px;
  }
  
  .card button:hover {
    opacity: 0.7;
  } 

  .btns{
      width: 80%;
      justify-content: center;
      align-items: center;
      text-align: center;
      margin-left: auto;
      margin-right: auto;
      margin-top: 2%;
  }

  .btns :first-child{
      margin-bottom: 2%;
  }